
@mixin breakpoint($point) {
    @if $point == small {
        @media screen and (max-device-width: 480px)
        and
        (orientation: landscape) {
            @content;
        }
    }
    @else if $point == medium {
        @media screen and (min-device-width: 768px)
        and
        (max-device-width: 1024px) {
            @content;
        }
    }
    @else if $point == large {
        @media screen and (min-width: 1024px) {
            @content;
        }
    }
}

