@use "../abstract" as *;
@use "../components/" as *;
@use "../utilities/" as *;

.mobile-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid white;
}
.logo {
    padding-left: .5em;
    border: 2px solid black;
}
header {
    position: relative;
    @include breakpoint(small) {
    ul {
        @include list-reset(reset-list);
        text-align: center;
        position: absolute;
        top: 40px;
        width: 70%;
        right: 10px;
    }
    li {
        border: 1px solid black;
        margin:  .3rem 0;
        &:hover {
            background-color: grey;
        }
    }
    a {
        text-decoration: none;
        color: inherit;
        display: block;
        width: 100%;
        // background-color: red;
        height: 100%;
        padding-block: .5em .2em;
    }
}
}
